
.foodWrap{padding: 0 0.2rem;
}
.foodWrap h3{color: #666666;height:0.5rem;line-height: 0.5rem;
}
.foodWrap h3 span{float: left;font-size: 0.3rem;
}
.foodWrap .title{border-bottom: 0.01rem solid #F3F3F3;padding-bottom: 0.05rem;
}
.foodWrap .title .pic{width: 0.6rem;height: 0.6rem;border-radius: 50%;float: left
}
.foodWrap .title .pic img{width: 0.6rem;height: 0.6rem;border-radius: 50%;
}
.foodWrap .title .right{float: left;padding-left: 0.2rem;
}
.foodWrap .title .right p{height: 0.3rem;line-height: 0.3rem;
}
.foodWrap .title .right span{height: 0.3rem;line-height: 0.3rem;color: #A3A3A3;font-size: 0.2rem;
}
.clearfix:after{content: '.';display: block;visibility: hidden;height: 0;clear: both;
}
.foodWrap .cont h3{text-align: left;color: #FF6767;font-weight: normal;height: 0.4rem;line-height: 0.4rem;font-size: 0.2rem;
}
.foodWrap .cont img{width: 2rem;
}
.foodWrap .cont button{width: 0.4rem;height: 0.4rem;border-radius: 50%;background: #FF6767;color: #fff;position: fixed;right: 10%;bottom: 15%;
}



