
.shopp{background:#fff;padding-bottom:15%;margin-bottom:5%;position:relative;width:100%
}
.shopp .pa{float:right
}
.shopp span{color:#f7a766
}
.shopp .center{position:relative;padding:3% 25%;background:#eee
}
.shopp .center p{line-height:25px;padding:0;position:relative;
}
.shopp .age{position:absolute;right:5%;top:45%
}
.shopp .center h3{font-size:16px;line-height:28px;
}
.shopp .pic{border-bottom:1px solid #ccc
}
.shopp img{width:20%;position:absolute;top:12px;left:3%
}
.shopp p{line-height:40px;padding:0 5%
}
.shopp button{position:absolute;bottom:4%;right:4%;border:1px solid #f7a766;background:#fff;border-radius:3px;padding:2%;color:#f7a766
}
.shopp .no{right:28%
}
.shopp .tt{padding:0 0 0 32%
}



