
*{margin:0;padding: 0;
}
.theme{background: #f7f7f7;margin-top: -0.05rem;
}
.foodsWrap h2{font-size: 0.35rem;text-align: left;padding-left: 0.2rem;height: 0.8rem;line-height: 0.8rem;
}
.foodsWrap ul{text-align: center;
}
.foodsWrap li{float: left;width: 25%;list-style: none;
}
.foodsWrap li a{font-size: 0.2rem;color: #666;text-decoration: none;display: inline-block;
}
.foodsWrap li img{width: 0.6rem;
}
.foodsWrap li span{line-height: 0.15rem;
}
.foodsWrap .active{background: ##FF7373
}
.foodsWrap .active a{color: ##FF7373
}
.foodsWrap .pic{width: 1.5rem;float: left;padding-top: 0.2rem;padding-right: 0.15rem;
}




