
.leaveWrap{width: 100%;
}
.leavetop input{width: 89%;margin: 5%;height:30px;border: 1px solid #ccc; padding-left: 5px;color: red;font-size: 0.7rem;color: #293646;
}
.leavetop textarea{width: 89%;height: 100px;border: 1px solid #ccc;margin-left: 5%;color: #293646;font-size: 0.7rem;list-style: none;display: block;padding: 5px 0 0 5px;font-weight: bold;
}
.leaveWrap button{width: 91%;height: 35px;background: #FF6767;border: none;margin-top: 5%;margin-left: 5%;color: #fff;font-size: 0.7rem;
}




