
.booknews{background:#f7f7f7;
}
.booknews li{width:80%;line-height:40px;border-bottom:1px solid #eee;padding:0 0 0 10%
}
.booknews ul{background:#fff;border-bottom:3px solid #eee
}
.booknews a{color:#969696;text-decoration:none;font-size:14px
}
.booknews .greed{border-bottom:2px solid #6bc27b
}
.booknews .greed a{color:#6bc27b;
}
.top span{position:absolute;top:45%;left:35%;color: #000;
}
.top img{width:100%;height:120px
}
.top{position:relative;color:#fff
}
.top .img{position:absolute
}
.top .img img{border-radius:50%;width:100%;height:100%
}
.top .img{width:20%;top:20%;left:10%;
}




