
.city{border:1px solid #ccc;width:85%;margin:0 auto;padding:4% 5% 0 5%;border-radius:5px;margin-top:5%
}
.city p{color:#ccc;font-size:14px;line-height:20px
}
.city .bo{color:#ccc;font-size:14px;line-height:50px;border-top:1px solid #eee;margin-top:5px
}
.city h3{font-size:16px;font-weight: normal;color:#66666;height:25px
}
.city .bo span{margin-left:20%
}
.citys{border:1px solid red
}



