
.coupon{background:#FF6767;padding-top:40px;height:520px
}
.coupon img{width:20%;position:absolute;top:-30px;left:40%
}
.coupon_nev{background:#fff;width:90%;margin:0 auto;border-radius:15px;margin-bottom:30px
}
.coupon_top{position:relative;text-align:center;padding:50px 0
}
.coupon_top p{line-height:30px;font-size:16px;color:#ccc
}
.coupon_top span{line-height:40px;font-size:20px;color:#ccc
}
.coupon_new{border-bottom:1px dashed #ccc;padding:0 5%
}
.coupon_new p{line-height:30px
}
.coupon_bot{padding:0 5%
}
.coupon_bot .cou{border-bottom:1px solid #ccc
}
.coupon_bot p{line-height:40px
}
.coupon button{border-radius:8px;background:#FFD63C;border:none;width:60%;line-height:40px;text-align:center;color:#fff;margin-left:20%;font-size:16px
}



